import { Button, TextField, Typography } from '@mui/material'
import axios from 'axios'
import URLS from 'constants/urls'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { sendRequest } from 'utils/requests'
import './Login.scss'

const Login = () => {
    const [login, setLogin] = useState('')
    const [password, setPassword] = useState('')
    const navigate = useNavigate()

    const setLoginData = async () => {
        const result = sendRequest('post', URLS.login, {
            username: login,
            password: password,
            grant_type: 'password'
        })
            .then(res => {
                if (res.requestSuccess) navigate('/ideas')
                else console.log('error');
            })
    }

    return (
        <main className='page-login'>
            <div className='page-login__content'>
                <h1 className='page-login__title'>   GreatIdea!</h1>
                <div className="page-login__form">
                    <TextField label="" variant="standard" value={login} onChange={(e) => setLogin(e.target.value)} />
                    <TextField label="" variant="standard" value={password} onChange={(e) => setPassword(e.target.value)} type='password' />
                    <div className="page-login__buttons">
                        <Button variant='outlined' onClick={() => navigate('/registration')}>
                            
                        </Button>
                        <Button variant='contained' disabled={!login || !password} onClick={setLoginData}>
                            
                        </Button>
                    </div>
                </div>
            </div>
        </main>
    )
}

export default Login